<template>
  <div class="auth">
    <div
      v-if="title"
      class="title"
    >
      {{ title }}
    </div>
    <breadcrumb
      v-else
      :back-path="backPath"
      :current-level="currentLevel"
      :upper-level="upperLevel"
    />
    <div class="wrap">
      <div class="content">
        <router-view />
      </div>
    </div>
  </div>
</template>
<script>
import './style.scss';
import breadcrumb from 'src/components/breadcrumb';
export default {
  components: {
    breadcrumb
  },
  data() {
    return {

    };
  },
  computed: {
    title() {
      const index = this.$route.path;
      const active = index.substring(index.lastIndexOf('/') + 1, index.length);
      switch (active) {
        case 'userManagement':
          return '用户管理';
        case 'roleManagement':
          return '角色管理';
        case 'authManagement':
          return '权限管理';
        default:
          return '';
      }
    },
    backPath() {
      return this.$route.meta.backPath || '';
    },
    currentLevel() {
      return this.$route.meta.currentLevel || '';
    },
    upperLevel() {
      return this.$route.meta.upperLevel || '';
    }
  },
  methods: {
  }
};
</script>



